import { ParallaxBanner } from "react-scroll-parallax";
import "./index.css";

const Goal = (props) => {
  const { img, text } = props;

  const headline = {
    translateY: [50, -40],
    scale: [1, 1.05, "easeOutCubic"],
    shouldAlwaysCompleteAnimation: true,
    expanded: false,
    children: (
      <div className="inset center">
        <h1 className="headline" style={{ color: "#282c34" }}>{text}</h1>
      </div>
    )
  };

  return (
    <div className="container-body goal-container">
      <ParallaxBanner
      layers={[ headline]}
      className="full"
    />
    </div>
  );
};
export default Goal;