import React, { Component } from "react";
 
class Ball extends Component {
    render() {
        const ballStyle = {
            backgroundColor: "#282c34",
            borderRadius: "50%",
            width: "3em",
            height: "2.25em",
            textAlign: "center",
            paddingTop: "0.75em",
            display: "inline-block",
            margin: "auto",
            color: "white",
            fontWeight: "bold",
            fontSize: "1.5em",
        };
        return (
            <div style={ballStyle} className="ball">{this.props.num}</div>
        );
    }
}
 
export default Ball;