import React from 'react'
import { useTrail, a } from '@react-spring/web'
import { Link } from "theme-ui";
import styles from './styles.css';
import CloseIcon from '@mui/icons-material/Close';

const Trail = ({ open, children }) => {
  const items = React.Children.toArray(children)
  const trail = useTrail(items.length, {
    config: { mass: 5, tension: 2000, friction: 200 },
    opacity: open ? 1 : 0,
    x: open ? 0 : 20,
    height: open ? 70 : 0,
    from: { opacity: 0, x: 20, height: 0 },
  })
  return (
    <div>
      {trail.map(({ height, ...style }, index) => (
        <a.div key={index} className={styles.trailsText} style={style}>
          <a.div style={{ height }}>{items[index]}</a.div>
        </a.div>
      ))}
    </div>
  )
}

export default function Drawer(props) {
  const { drawOpen, setDrawOpen, homeRef, serviceRef } = props;
  const handleClick = (ref) => {
    ref.current?.scrollIntoView({ behavior: 'smooth' });
  };
  return (
    <div className={styles.container} onClick={() => setDrawOpen(false)}>
      <Trail open={drawOpen}>
        <div className='draw-close-con'>
          <CloseIcon className='draw-close' onClick={() => setDrawOpen(false)}></CloseIcon>
        </div>
          <Link className="draw-links" onClick={() => handleClick(homeRef)}>HOME</Link>
          <Link className="draw-links" onClick={() => handleClick(serviceRef)}>SERVICES</Link>
          <Link className="draw-links">PRODUCTS</Link>
          <Link className="draw-links">CONTACT</Link>
        
      </Trail>
    </div>
  )
}
