import { ParallaxBanner } from "react-scroll-parallax";
import "./index.css";
const Window = (props) => {
  const { img, text } = props;
  return (
    <div className="container-body">
      <ParallaxBanner
        layers={[
          {
            speed: -15,
            children: (
              <div className="">
                <h1 className="text">{text ? text : '' }</h1>
              </div>
            ),
          },
          
          { image: img, speed: -10 },
        ]}
        
        className="bg-container"
      >
        <div className="bg-container">
          <h1 className=""></h1>
        </div>
      </ParallaxBanner>
    </div>
  );
};
export default Window;