import "./index.css";
import {MovingComponent} from "react-moving-text";
import { useState } from 'react'
import 'react-animated-slider/build/horizontal.css';
import Card from "../card/index";
import { Parallax } from 'react-parallax';
import Ball from "../ball";

export const Basic = (props) => {
    const [selectedId, setSelectedId] = useState(null)
    const { title, content, width, cssClass, list, details, flow } = props;
    return (
        
        <div style={{ width: width }} className={cssClass}>
            {title && <h2>{title}</h2>}
            {content && <p>{content}</p>}
            {list && 
                
                <MovingComponent type="typewriter" timing="ease"
                dataText={list} className="services"/>
            }
            {details && 
                  <Card details={details} />
            }
            {flow && 
                    <div class="flow-container">
                        <div class="flow-sub">
                            <div>
                                <p>{flow[0]}</p>
                                <Ball num={1}></Ball>
                            </div>
                            <div>
                                <p>{flow[1]}</p>
                                <Ball num={2}></Ball>
                            </div>
                        </div>
                        <div class="flow-sub">
                            <div>
                                <p>{flow[2]}</p>
                                <Ball num={3}></Ball>
                            </div>
                            <div>
                                <p>{flow[3]}</p>
                                <Ball num={4}></Ball>
                            </div>
                        </div>
                    </div>
            }
        </div>
    )
}