import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';

import './index.css';
import bgimage from '../../assets/images/slide-bg.png';


import { Parallax, Pagination, Navigation,EffectCoverflow } from 'swiper/modules';

const Card = (props) => {
  const { details } = props;
  
  return (
<>
<Swiper
        style={{
          '--swiper-navigation-color': '#fff',
          '--swiper-pagination-color': '#fff',
        }}
        speed={600}
        parallax={true}
        pagination={{
          clickable: true,
        }}
        navigation={true}
        modules={[Parallax, Pagination, Navigation]}
        className="mySwiper"
      >
        <div
          slot="container-start"
          className="parallax-bg blur-bg"
          style={{
            backgroundImage:
              'url('+bgimage+')'
          }}
          data-swiper-parallax="-23%"
        ></div>
        {details.map((value) => (
          <SwiperSlide key={value.id}>
            <div className="subtitle glow" data-swiper-parallax="-200">
            {value.title}
              </div>
              <div className="text glow" data-swiper-parallax="-100">
                <p>
                {value.content}
                </p>
          </div>
              
          </SwiperSlide>
          ))}
          
      </Swiper>
      
    </>
  )
};
export default Card;