import { Box, Link } from "theme-ui";
import logo from "../../assets/images/logo.png";
import "./index.css"
import MenuSharpIcon from '@mui/icons-material/MenuSharp';
import { useEffect } from "react";
import { StyledNavContainer } from "./style";

export const Header = (props) => {
  const { headerStyle, menuHandle, homeRef, serviceRef } = props;
  useEffect(() => {
  }, [])
  const handleClick = (ref) => {
    ref.current?.scrollIntoView({ behavior: 'smooth' });
  };

  return (
    <StyledNavContainer style={headerStyle} className="header">
      <Box className="logo-con">
        <img src={logo} className="logo"/>
      </Box>
      <Box className="nav-bar">
        <Box className="nav-btns">
          <Link className="links" onClick={() => handleClick(homeRef)}>HOME</Link>
          <Link className="links" onClick={() => handleClick(serviceRef)}>SERVICES</Link>
          <Link className="links">PROJECTS</Link>
          <Link className="links">CONTACT</Link>
        </Box>
        <Box className="nav-menu">
          <MenuSharpIcon fontSize={'large'} className="menu-btn" onClick={() => menuHandle()}></MenuSharpIcon>
        </Box>
      </Box>
    </StyledNavContainer>
    
  );
};
