import styled from "@emotion/styled";
import { animated } from "react-spring";

export const StyledNavContainer = styled(animated.nav)`
  display: flex;
  justify-content: space-between;
  backdrop-filter: saturate(180%) blur(5px);
  background-color: #282c34;
  height: 80px;
  position: relative;
  width: 100%;
`;

export const StyledSubLinksWrapper = styled(animated.div)`
  display: flex;
  align-items: center;
`;
