import React, { useEffect, useState, useRef } from "react";
import './App.css';
import { Header } from './components/header/index';
import { StyledNavWrapper } from "./style";
import { useSpring } from "react-spring";
import "./base.css";
import Home from "./assets/images/home.png";
import { Box } from "theme-ui";
import Drawer from "./components/drawer/index";
import { Reveal } from "./components/reveal";
import { Basic } from "./components/basic";
import { Firm, Service, Flow } from "./assets/images/contents";
import Goal from "./components/goal";
import { ParallaxProvider } from "react-scroll-parallax";
import Window from "./components/window";
import window1 from "./assets/images/window1.png";
import window2 from "./assets/images/window2.png";
import ScrollButton from "./components/scrollbtn";

function About() {
  const [topHeaderPosition, setTopHeaderPosition] = useSpring(() => ({
    position: "fixed",
    transform: "translate3d(0px, 0px, 0px)",
    immediate: true,
    borderBottom: "0px solid #ddd",
  }));

  const homeRef = useRef(null);
  const serviceRef = useRef(null);

  

  const [showDraw, setShowDraw] = useState(false);
  const [position, setPosition] = useState(window.scrollY)
    const [visible, setVisible] = useState(true) 
    useEffect(()=> {
        const handleScroll = () => {
           let moving = window.scrollY
           
           setVisible(position > moving);
           setPosition(moving)
        };
        window.addEventListener("scroll", handleScroll);
        return(() => {
           window.removeEventListener("scroll", handleScroll);
        })
    })

  const cls = visible ? "visible" : "hidden";

  function menuHandle() {
    setShowDraw(true);
  };

  const [isLoading, setIsLoading] = React.useState(true);

  const handleLoading = () => {
    setIsLoading(false);
  }

  useEffect(()=>{
    window.addEventListener("load",handleLoading);
    return () => window.removeEventListener("load",handleLoading);
  },[])

  

  return (
    <React.Fragment>
      <Box>
          <StyledNavWrapper ref={homeRef} className={"header-bar "+cls}>
            <Header homeRef={homeRef} serviceRef={serviceRef}
              headerStyle={{ ...topHeaderPosition }}
              menuHandle={menuHandle}
            />
          </StyledNavWrapper>
          <Reveal className="home-con">
            <img src={Home} width={"70%"}/>
          </Reveal>
          <Reveal className="contents-con" >
            <Basic title={Firm.title} content={Firm.content} cssClass={"basic"}></Basic>
            <ParallaxProvider>
            <Window img={window1}></Window>
            </ParallaxProvider>
          </Reveal>
          <Reveal className="contents-con goal-container" >
            <ParallaxProvider>
              <Goal text={"CREATING AESTHETIC AND FUNCTIONAL SPACES FOR BETTER ENVIRONMENT"}></Goal>
            </ParallaxProvider>
          </Reveal>
          <div ref={serviceRef}>
          <Reveal className="contents-con" >
            <ParallaxProvider>
              <Window img={window2}></Window>
            </ParallaxProvider>
            <Basic title={Service.title} content={Service.content} cssClass={"basic"} list={Service.list}></Basic>
          </Reveal>
          </div>
          <Reveal className="contents-con">
            <Basic cssClass={"service-cards"}  width={"87%"} details={Service.details}></Basic>
          </Reveal>
          <Reveal className="contents-con flow-main">
            <Basic title={"HOW WE OPERATE"} width={"87%"} cssClass={"basic flow-main"} flow={Flow}></Basic>
          </Reveal>
      </Box>
      <div className="draw-overlay" style={{ display : showDraw ? "flex" : "none" }}>
        <Drawer drawOpen={showDraw} setDrawOpen={setShowDraw} homeRef={homeRef} serviceRef={serviceRef}></Drawer>
      </div>
    </React.Fragment>
  );
}
export default About;