import React, { useState, useEffect, useRef } from 'react';
import BgSlide from './components/bgslide';
import homeimg from './assets/images/home-white.png';
import './home.css';
import logo from './assets/images/logo.png';
import {ReactNavbar} from "overlay-navbar";

const HomePage = () => {
  const navRef = useRef(null);
    const params = {
      burgerColor: "white",
      logo: logo,
      logoHoverColor: "lightgrey",
      link1Text: "",
      link1Url: "",
      link3Text: "WORKS",
      link3Url: "/works",
      link2Text: "",
      link2Url: "",
      link4Text: "ABOUT",
      link4Url: "/about",
      link1Color: "white",
      link1Family: "'Segoe UI', Tahoma, Geneva, Verdana, sans-serif",
      link1Size: "25px !important",
      logoWidth: "150px",
      nav1Transition: "0.8",
      nav2Transition: "1.6",
      nav3Transition: "2.4",
      nav4Transition: "3.2",
      nav3FlexDirection: "column",
      link3Margin: "50px"
  };

  const triggerClick = () => {
      var element = document.getElementsByClassName('menuBurger');
      element[0].click();
  };

  return (
    <div>
      <ReactNavbar ref={navRef} {...params}/>
      <BgSlide />
      <div className='home-overlay'>
        <div className='home-container'>
          <img className='home-logo' width={"300px"} src={homeimg}/>
          <p className='home-info'>CREATING AESTHETIC AND FUNCTIONAL SPACES FOR BETTER ENVIRONMENT</p>
          <p className='home-details'>For more details. <a onClick={triggerClick}>Click here</a></p>
        </div>
      </div>
    </div>
  );
};

export default HomePage;